  display: block;
  content: "";
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  width: 26px;
  height: 26px;
}

.nv-alert {
  position: fixed;
  top: 25%;
  right: 0;
  left: 0;
  width: 350px;
  max-width: calc(100vw - 1rem);
  margin: auto;
  z-index: 99999999999999;
  display: none;
}
.nv-alert.show {
  display: block;
}
.nv-alert.nv-info {
  --bs-alert-padding-y: 1.75rem;
  box-shadow: var(--bs-box-shadow);
}

