/* Table */
figure.table .ck-table-resized {
  table-layout: fixed;
}

figure.table table {
  overflow: hidden;
}

figure.table td,
figure.table th {
  overflow-wrap: break-word;
  position: relative;
}

figure.table {
  margin: 5px auto 10px auto;
  display: table;
}

figure.table table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  height: 100%;
  border: 1px double var(--bs-border-color);
}

figure.table table td,
figure.table table th {
  min-width: 5px;
  padding: 7px;
  border: 1px solid var(--bs-border-color);
}

figure.table table th {
  font-weight: bold;
  border-bottom-width: 2px;
}

figure.table > figcaption {
  display: table-caption;
  caption-side: top;
  word-break: break-word;
  text-align: center;
  outline-offset: -1px;
  margin-top: 0;
}

/* Media */
figure.media {
  clear: both;
  margin: 5px 0 10px 0;
  display: block;
  min-width: 10px;
}

/* NV-Media */
figure.nv-media {
  clear: both;
  margin: 5px 0 10px 0;
  display: block;
  min-width: 10px;
}

figure.nv-media video,
figure.nv-media audio {
  max-width: 100%;
  margin: 0 auto;
  display: block;
}

/* Image */
img.image_resized {
  height: auto;
}

figure.image.image_resized {
  max-width: 100%;
  display: block;
  box-sizing: border-box;
}

figure.image.image_resized img {
  width: 100%;
}

figure.image.image_resized > figcaption {
  display: block;
}

figure.image {
  display: table !important;
  /* Important  khng xung t khi copy t Google Docs */
  clear: both;
  text-align: center;
  margin: 5px auto 10px auto;
  min-width: 10px;
}

figure.image img {
  display: block;
  margin: 0 auto;
  max-width: 100%;
  min-width: 100%;
  height: auto;
}

figure.image-inline {
  display: inline-flex;
  max-width: 100%;
  align-items: flex-start;
}

figure.image-inline picture {
  display: flex;
}

figure.image-inline picture,
figure.image-inline img {
  flex-grow: 1;
  flex-shrink: 1;
  max-width: 100%;
}

figure.image > figcaption {
  display: table-caption;
  caption-side: bottom;
  word-break: break-word;
  padding: 7px 7px 0 7px;
  font-size: 13px;
  outline-offset: -1px;
  margin-top: 0;
}

